@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);

body {
    font-family: 'Montserrat', sans-serif;
}

.circle-icon {
    color: #385170;
    padding: 0.5rem;
    border-radius: 100%;
    background: #fff;
    top: 10px;
    right: 20px;
    cursor: pointer;
    position: absolute;
}

.circle-icon:hover {
    color: #142d4c;
}

.btn-link {
    padding: 0.25rem;
    margin: 0.5rem auto;
    color: #142d4c;
    border: 1px solid #142d4c;
    color: #fff;
    background-color: #385170;
    cursor: pointer;
    font-weight: bolder;
}

.page-heading {
    color: #fff;
    background-color: #222831;
    padding: 0.5rem 1rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0.5rem;
    border-radius: 25px;
}

.playlist-wrapper, .liked-list, .watch-list {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.playlist-wrapper {
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

.empty-container {
    width: 70%;
    
    margin: 2rem auto;
}

.empty-container img {
    width: 70%;
    height: 50vh;
}

@media (max-width: 720px) {
    .empty-container img {
        height: auto;
    }
}
/* For Navbar */


.navbar {
  background-color: #141b25;
}

.main-nav {
  height: 90px;
}

.logo {
  color: white;
  line-height: 90px;
  font-size: 30px;
  font-weight: bold;
  text-decoration: none;
  margin-left: 30px;
  /* font-family: "AstroSpace", sans-serif; */
}

.navlinks {
  list-style: none;
  float: right;
  line-height: 90px;
  margin: 0;
  padding: 0;
}

.navlinks li {
  display: inline-block;
  margin: 0px 20px;
}

.navlinks li .link {
  color: white;
  text-decoration: none;
  font-size: 18px;
  transition: all 0.3s linear 0s;
  text-transform: uppercase;
}

.navlinks li .link:hover {
  color: #7ebcb9;
  padding-bottom: 7px;
  border-bottom: 2px solid #7ebcb9;
}


#check {
  display: none;
}

.menu-btn {
  font-size: 25px;
  color: white;
  float: right;
  line-height: 90px;
  margin-right: 40px;
  display: none;
  cursor: pointer;
}

@media (max-width: 800px) {
  .navlinks {
    position: fixed;
    width: 100%;
    height: 100vh;
    text-align: center;
    transition: all 0.5s;
    right: -100%;
    background: #222831;
    z-index: 10;
  }

  .navlinks li {
    display: block;
  }

  .navlinks li .link {
    font-size: 20px;
  }

  .navlinks li .link:hover {
    border-bottom: none;
  }

  .menu-btn {
    display: block;
  }

  #check:checked ~ .navlinks {
    right: 0;
  }
}

@media (max-width: 910px) {
  .logo {
    font-size: 25px;
  }

  .navlinks li .link {
    font-size: 15px;
  }
}
/* For PlaylistModal */

.modal {
    border: 2px solid #fff;
    position: absolute;
    padding: 1rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background-color: #9fd3c7;
    border-radius: 20px;
    top: 30vh;
}

.modal-show {
    display: block;
}

.modal-hide {
    display: none;
}

.modal-title {
    color: #385170;
}

.modal-input input {
    border: 0px solid;
    border-bottom: 2px solid #142d4c;
    padding: 0.5rem 1rem;
    width: 200px;
}

.modal-input input:focus {
    outline: none;
}

.modal-btn {
    border: 0px solid;
    padding: 0.5rem 1rem;
    margin: auto 1rem;
    color: #142d4c;
    font-weight: bold;
    cursor: pointer;
}

.playlist-names {
    font-size: 20px;
    margin: 1rem;
    font-weight: bold;
    color: #142d4c;
}



@media (max-width:420px) {
    .modal {
        padding: 1rem 0.25rem;
    }

    input {
        padding: 0.5rem 0.25rem;
    }
}
/* For VideoPlayer */

.video-player-container {
    margin: 0.5rem;
    padding: 1rem;
    border: 0.5px solid gray;
    border-radius: 10px;
}

.player {
    border-bottom: 2px solid #142d4c;
    height: 50vh;
}

.heading {
    color: #142d4c;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.text {
    font-size: large;
    background-color: #142d4c;
    color: #fff;
    padding: 0.5rem;
    border-radius: 5px;
}

.category {
    background-color: #9fd3c7;
    padding: 0.25rem;
    border: 1px solid #9fd3c7;
    border-radius: 5px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.btn-controls {
    text-align: right;
    margin: 0.5rem;
}

.btn-controls span {
    padding: 0.5rem;
    cursor: pointer;
    border: 1px solid #142d4c;
    border-radius: 5px;
    color: #142d4c;
    margin: auto 0.25rem;
}

.btn-controls span:hover {
    background-color: #9fd3c7;
}

@media (max-width: 420px) {
    .btn-controls span{
        display: block;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        margin: 1rem auto;
    }
}
/* For VideoCard */

.card {
    width: 300px;
    display: block;
    margin: 1rem auto;
    padding: 0.5rem;
    border: 2px solid #142d4c;
    border-radius: 10px;
    position: relative;
    /* z-index: -1; */
}

.card img {
    width: 300px;
    margin: auto;
    border-radius: 10px;
}

.card img:hover {
    opacity: 0.7;
}

.card-body {
    color: #142d4c;
    margin: auto;
    text-align: center;
}

.card-text {
    padding: 0.25rem;
    border: 1px solid grey;
    color: gray;
    border-radius: 5px;
}

@media (max-width: 786px) {
    .card, .card img {
        width: 200px;
    }
}
/* For Videos.jsx */

.videos-container {
    padding: 1rem;
    background-color: #ececec;
}

.wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
/* For Login and Signup */

.form-container {
    border: 2px solid #142d4c;
    padding: 1rem;
    margin: 1rem auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-radius: 10px;
    text-align: center;
}

.form h1 {
    color: #385170;
}

.form-label {
    margin: 2rem;
}

.form-label label {
    padding: 0.5rem 1rem;
    margin: auto 0.5rem;
    background-color: #385170;
    color: #fff;
}

.form-label input {
    border: 0px solid;
    padding: 0.5rem 1rem;
    font-size: 15px;
    background-color: #9fd3c7;
    color: #142d4c;
}

.form-label input:focus {
    outline: none;
    border-bottom: 2px solid #142d4c;
}

.btn-auth {
    padding: 0.5rem 1rem;
    margin: 1rem;
    color: #fff;
    background-color: #385170;
    font-size: 20px;
    border: 0px solid;
    width: 70%;
}

.btn-auth:hover {
    background-color: #142d4c;
    cursor: pointer;
}

.form-message {
    color: #142d4c;
    background-color: #fff;
    padding: 0.5rem;
}

.message-link  {
    text-decoration: none;
    margin: auto 0.25rem;
    color: #385170;
    border: 2px solid #385170;
    padding: 0.25rem;
    border-radius: 5px;
    cursor: pointer;
}

.message-link:hover {
    opacity: 0.8;
}
/* For userDetails.jsx  */

.user-container {
    margin: 3rem auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 2rem;
    border: 3px solid #142d4c;
    border-radius: 5px;
}

h3 {
    color: #fff;
    padding: 0.5rem;
    background-color: #142d4c;
    border-radius: 10px;
    text-align: center;
}

.username {
    font-size: 20px;
    border-bottom: 2px solid #142d4c;
    padding: 0.5rem;
    background-color: #9fd3c7;
    color: #142d4c;
}

.username strong {
    color: gray;
}

.user-email {
    color: #142d4c;
    padding: 0.5rem;
    background-color: gray;
    font-size: 20px;
    border-bottom: 2px solid #142d4c;
}

.user-email i {
    color: #9fd3c7;
}

.btn-logout {
    padding: 0.5rem 0.5rem;
    text-align: center;
    width: 100%;
    color: #142d4c;
    border: 2px solid #142d4c;
    border-radius: 5px;
    font-size: 20px;
    font-weight: bold;
    background-color: #fff;
}

.btn-logout:hover {
    cursor: pointer;
    background-color: #9fd3c7;
}
